*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: teal;
}
.main{
    text-align: center;
    margin-top: 30px;
}
.main input{
    padding: 10px;
    border: 2px solid salmon;
    border: none;
    border-radius: 10px;
    background-color: white;
}
.main button{
    padding: 5px; 
    background-color: red;
    color: white;
    font-size: large;
    margin-top: 5px;
    border: none;
    border-radius: 5px;
}
.navclass{
    color: turquoise;
    font-size: 40px;
    font-weight: bold;
    
    
}
nav{
    padding: 10px 0;
    display: flex;
    justify-content: center;
    margin-top: 10px;
    cursor: pointer;
    background-color: violet;
}
nav ul{
    display: flex;
    list-style: none;
    justify-content: space-between;
    width: 40%;
    align-items: center;
    font-weight: bolder;
    background-color: violet;
}
nav ul li{
    background-color: violet;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
}
nav ul li:hover{
    transform: scale(1.2);
}
.answer h3{
    background-color: brown;
    padding: 10px;
    text-align: center;
    font-size: xx-large;
}
